import 'parsleyjs';

window.Parsley.addValidator('phone', {
    requirementType: 'string',
    validateString: function(value) {
        return /^(\+7|7|8)?[\s\-]?\(?[489][0-9]{2}\)?[\s\-]?[0-9]{3}[\s\-]?[0-9]{2}[\s\-]?[0-9]{2}$/.test(value);
    },
    messages: {
        en: 'This value should be a mobile number',
        ru: '    '
    }
});

window.Parsley.addValidator('cardnumber', {
    requirementType: 'string',
    validateString: function(value) {
        return /^[0-9]{16}$/.test(value.replace(/\s+/g, ''));
    },
    messages: {
        en: 'Enter correct card number',
        ru: '   '
    }
});

window.Parsley.addValidator('cardcvv', {
    requirementType: 'string',
    validateString: function(value) {
        return /^[0-9]{3}$/.test(value.replace(/\s+/g, ''));
    },
    messages: {
        en: 'Enter correct card CVV',
        ru: '  CVV '
    }
});
window.Parsley.addValidator('carddate', {
    requirementType: 'string',
    validateString: function(value) {
        console.log('  ', value);

        return /^[0-9]{2}\/[0-9]{2}$/.test(value.replace(/\s+/g, ''));
    },
    messages: {
        en: 'Enter correct period',
        ru: '   '
    }
});

window.Parsley.addValidator('requiredIfChecked', {
    requirementType: 'string',
    validateString: function(value, requirement) {
        console.log('Validating', value);

        const checkbox = document.querySelector(requirement);

        if (!checkbox) {
            return false;
        }

        if (checkbox.checked && !value.trim()) {
            return false;
        }

        return true;
    },
    messages: {
        en: 'Required field',
        ru: ' '
    },
    priority: 33
});

Parsley.addMessages('ru', {
    defaultMessage: ' .',
    type: {
        email: '      E-mail',
        url: '   .',
        number: ' .',
        integer: '  .',
        digits: '  .',
        alphanum: ' - .'
    },
    notblank: '    .',
    required: ' ',
    pattern: '  .',
    min: '       %s.',
    max: '       %s.',
    range: '     %s  %s.',
    minlength: '      %s .',
    maxlength: '      %s .',
    length: '     %s  %s .',
    mincheck: '   %s .',
    maxcheck: '   %s .',
    check: '  %s  %s .',
    equalto: '   .'
});

Parsley.setLocale('en');

export default function validation() {

    function initializeValidation() {
        const formsToValidate = Array.from(document.querySelectorAll('form[data-need-validation]'));

        formsToValidate.forEach(form => {
            $(form).parsley({});
        });
    }
    

    window.initializeValidation = initializeValidation;

    initializeValidation();
    
}
